import { getTransactions } from "@/lib/actions/transactions";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export default async function TransactionsPage() {
  const transactions = await getTransactions();

  return (
    <div className="space-y-6 p-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">Transactions</h1>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Recent Activity</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {transactions.length === 0 ? (
                <div className="text-center py-10 text-muted-foreground">
                    No transactions found. Upload a statement to get started.
                </div>
            ) : (
                transactions.map((tx) => (
                <div
                    key={tx.id}
                    className="flex items-center justify-between p-4 border rounded-lg hover:bg-muted/50 transition-colors"
                >
                    <div className="space-y-1">
                    <div className="font-medium">{tx.description}</div>
                    <div className="text-sm text-muted-foreground">
                        {new Date(tx.date).toLocaleDateString()} â€¢ {tx.accountSource || "Unknown Source"}
                    </div>
                    </div>
                    <div className="flex items-center gap-4">
                    {tx.category1 && (
                        <Badge variant="outline">{tx.category1}</Badge>
                    )}
                    <div className={`font-bold ${tx.amount < 0 ? "" : "text-green-600"}`}>
                        {new Intl.NumberFormat("de-DE", {
                        style: "currency",
                        currency: "EUR",
                        }).format(tx.amount)}
                    </div>
                    </div>
                </div>
                ))
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
